.tui-pagination {
  height: 28px;
  margin: 0 auto;
  line-height: normal;
  text-align: center;
  font-size: 0;
  @include min_1024 {
    margin: 20px 0 0;
  }
}

.tui-page-btn {
  display: inline-block;
  position: relative;
  width: 22px;
  padding: 8px 0 6px;
  margin-left: 0;
  font-size: 12px;

  font-weight: normal;
  line-height: 1;
  text-decoration: none;
  vertical-align: middle;
  border: 1px solid #ddd;
  border-width: 2px 0;
  @extend %transition;
  @include min_768 {
    width: 35px;
    padding: 10px 0;
    font-size: 14px;
  }
  @include min_1440 {
    font-size: 18px;
    width: 50px;
  }
  &:hover,
  &:focus,
  &:active {
    background-color: $decorativeColorLight;
    border-color: $decorativeGreyColor;
    color: $textColorLightTheme;
  }
}

.tui-is-selected,
.tui-pagination strong {
  color: $textColorDarkTheme;
  background-color: $decorativeColor;
  @extend %transition;
  cursor: default;
  &:hover,
  &:focus {
    background-color: $decorativeColor;
  }
}

.tui-first-child {
  &.tui-is-selected {
    margin-left: 0;
    border-left: 2px solid #ddd;
  }
  border-left: 2px solid #ddd;
}

.tui-last-child {
  &.tui-is-selected {
    border-right: 1px solid $decorativeColorDark;
  }
}

.tui-prev-is-ellip,
.tui-last-child,
.tui-next-is-ellip {
  border-left: 1px solid #ddd;
}

.tui-pagination strong.tui-first-child {
  margin-left: 0;
}

.tui-first,
.tui-prev,
.tui-next,
.tui-last,
.tui-prev-is-ellip,
.tui-next-is-ellip {
  overflow: hidden;
  height: 26px;
  padding: 0;
  font-size: 0;
  line-height: 26px;
  white-space: nowrap;
  font-weight: normal;
  border: 1px solid #ddd;
  @include min_768 {
    height: 30px;
  }
}

.tui-prev-is-ellip {
  border-right: 0;
}

.tui-next-is-ellip {
  border-left: 0;
}

.tui-first + .tui-prev,
.tui-next + .tui-last {
  border-left: 0;
}

.tui-ico-first,
.tui-ico-prev,
.tui-ico-next,
.tui-ico-last,
.tui-ico-ellip {
  background: url(data:image/png;base64,)
    no-repeat 0 0;
  display: inline-block;
  vertical-align: middle;
  font-size: 0px;
}

.tui-prev-is-ellip,
.tui-next-is-ellip {
  padding: 0;
  line-height: 1;
}

.tui-prev {
  margin-right: 4px;
  @include min_768 {
    margin-right: 10px;
  }
}

.tui-next {
  margin-left: 4px;
  @include min_768 {
    margin-left: 10px;
  }
}

.tui-ico-first {
  width: 11px;
  height: 10px;
  background-position: 0px 0px;
}

.tui-ico-prev {
  width: 6px;
  height: 10px;
  background-position: -26px 0px;
}

.tui-ico-next {
  width: 6px;
  height: 10px;
  background-position: -13px -24px;
}

.tui-ico-last {
  width: 11px;
  height: 10px;
  background-position: 0px -12px;
}

.tui-ico-ellip {
  width: 10px;
  height: 2px;
  margin-top: 16px;
  vertical-align: top;
  background-position: 0px -24px;
}

.tui-is-disabled:hover {
  background: transparent;
  border-color: #ddd;
  cursor: default;
}

.tui-is-disabled .tui-ico-prev {
  background-position: -26px -12px;
}

.tui-is-disabled .tui-ico-first {
  background-position: -13px 0px;
}

.tui-is-disabled .tui-ico-next {
  background-position: -21px -24px;
}

.tui-is-disabled .tui-ico-last {
  background-position: -13px -12px;
}

.tui-page-inputs {
  display: inline-block;
  vertical-align: top;
}

.tui-page-input {
  display: inline-block;
  width: 10px;
  height: 12px;
  padding: 8px 0 6px;
  font-size: 12px;
  text-align: center;
  color: #333;
  border: 0;
  outline: none;
}

.tui-page-inputs .tui-page-current {
  color: #4b96e6;
}

.tui-page-separator {
  display: inline-block;
  font-size: 12px;
  height: 12px;
  padding: 8px 0 6px;
  vertical-align: top;
}
